.row {
  margin-left: 20px;
  color: #fff;
}

.row h2 {
  margin-left: 10px;
}

.row_posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px 0;
  scrollbar-width: thin;
}

.row_posters::-webkit-scrollbar {
  display: none;
}

.row_poster {
  max-height: 100px;
  object-fit: contain;
  margin-right: 10px;
  transition: transform 0.2s;
  cursor: pointer;
}

.row_poster:hover {
  transform: scale(1.08);
  opacity: 1;
}

.row_posterLarge {
  max-height: 250px;
}

.row_posterLarge:hover {
  transform: scale(1.1);
  opacity: 1;
}
/* Row YouTube Trailer */
.row_trailer {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.row_trailer iframe {
  border: none;
  border-radius: 10px;
  max-width: 100%;
  width: 80%;
  height: 450px;
  box-shadow: 0 0 20px rgba(0,0,0,0.5);
}
